import { makeAutoObservable } from 'mobx';
import shuffle from 'lodash/shuffle';
import cards from '../data/cards';
import end from '../data/end';

const maxInHand = 7;
const maxToEndRound = 4;

const getRandom = (min, max) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1) + min);
}

class GameStore {
  constructor() {
    makeAutoObservable(this);
  }

  deck = shuffle([...cards, ...cards, ...end]);

  discard = [];
  board = [];

  players = [
    { hand: [], rainbow: [] },
    { hand: [], rainbow: [] },
    { hand: [], rainbow: [] },
    { hand: [], rainbow: [] },
  ];

  phase = 'normal'; // normal, pull

  activePlayer = 0;
  activePullPlayer = 0;
  pullLine = [];
  pulled = 0;

  pullPlayer(p, num = maxInHand) {
    if (this.players[p].hand.length + num > maxInHand) return;
    const d = [...this.deck];
    this.players[p].hand = [...this.players[p].hand, ...d.splice(0, num)];
    this.deck = d;
  }

  pullCards() {
    this.players.forEach((p, i) => {
      const hand = this.players[i].hand.length;
      if (hand < 5) this.pullPlayer(i, maxInHand - hand);
    });
  }

  updateActivePlayer() {
    this.activePlayer++;
    if (this.activePlayer > this.players.length - 1) {
      this.activePlayer = 0;
      this.pullCards();
      if (this.deck.length < this.players.length) {
        this.deck = [...this.deck, ...shuffle(this.discard)];
        this.discard = [];
      }
    }
  }

  updateActivePullPlayer() {
    this.pulled++;
    if (this.pulled === 2) return; // 2 player can pull twice
    this.activePullPlayer++;
    // if (this.activePullPlayer > this.players.length - 1) {
    //   this.activePullPlayer = 0;
    // }
    if (this.activePullPlayer >= this.players.length - 1) {
      this.endPullRound();
    }
  }

  endRound() {
    this.phase = 'pull';
    let pull = [];
    for(let i = 0; i < this.players.length; i++) {
      if (i !== this.activePlayer) pull.push(i);
    }
    pull = shuffle(pull);
    pull.splice(1, 0, this.activePlayer);
    this.pullLine = pull;
    this.activePullPlayer = 0;
    this.activePlayer = pull[pull.length -1];
  }

  endPullRound() {
    this.pulled = 0;
    this.discard = [...this.discard, ...this.board];
    this.board = [];
    // this.activePlayer = this.activePullPlayer;
    this.pullCards();
    this.phase = 'normal';
  }

  pullToRainbow(c) {
    // if (this.phase !== 'pull' || ![0, this.board.length - 1].includes(c)) return;
    if (this.phase !== 'pull') return;

    const line = this.players[this.pullLine[this.activePullPlayer]].rainbow;
    const firstInLine = line[0] || [];
    const lastInLine = line[line.length - 1] || [];
    const pushed = this.pushWithRotate(this.board[c], line, firstInLine, lastInLine);
    if (pushed === false) return;
    this.checkWinner();

    // this.players[this.activePullPlayer].rainbow.push(this.board[c]);
    this.board.splice(c, 1);
    this.updateActivePullPlayer();
  }

  step(p, c) {
    if (p !== this.activePlayer) return;

    const card = this.players[p].hand[c];

    if (card[0] === '#848484') {
      if (this.board.length >= maxToEndRound) {
        this.discard.push(this.players[p].hand[c]);
        this.players[p].hand.splice(c, 1);
        this.endRound();
      }
      return;
    }

    const firstInLine = this.board[0] || [];
    const lastInLine = this.board[this.board.length - 1] || [];
    const pushed = this.pushWithRotate(card, this.board, firstInLine, lastInLine);
    if (pushed === false) return;

    this.players[p].hand.splice(c, 1);

    this.updateActivePlayer();
  }

  pushWithRotate(card, line, firstInLine, lastInLine) {
    if (!line.length) {
      console.log('Start');
      line.push(card);
    } else if (card.includes(firstInLine[0])) {
      if (card[0] === firstInLine[0]) card.reverse();
      line.unshift(card);
    } else if (card.includes(lastInLine[1])) {
      if (card[0] !== lastInLine[1]) card.reverse();
      line.push(card);
    } else {
      return false;
    }
  }

  skipStep() {
    const random = getRandom(0, this.players[this.activePlayer].hand.length - 1);
    this.discard.push(this.players[this.activePlayer].hand[random]);
    this.players[this.activePlayer].hand.splice(random, 1);
    this.updateActivePlayer();
  }

  skipPullStep() {
    this.updateActivePullPlayer();
  }

  checkWinner() {
    this.players.forEach((player, i) => {
      const colors = player.rainbow.reduce((a, card) => ({ ...a, [card[0]]: 1, [card[1]]: 1 }), {});
      if (Object.keys(colors).length >= 7) alert(`Player ${i + 1} win!`);
    });
  }
}

const gameStore = new GameStore();

export default gameStore;
