import React, { useEffect } from 'react';
import gameStore from './store/gameStore';
import { observer } from 'mobx-react';
import cn from 'classnames';

const App = () => {
  useEffect(() => {
    gameStore.players.forEach((p, i) => {
      gameStore.pullPlayer(i);
    })
  }, []);

  return (
    <div className="wrapper">
      <h1 className="phase">Phase - {gameStore.phase}</h1>
      <div className="board">
        <h1>Board</h1>
        <div className="game-area">
          {
            gameStore.board.map((card, c) => <div className="card" onClick={() => gameStore.pullToRainbow(c)}>
              <div style={{background: card[0]}} className="side top" />
              <div style={{background: card[1]}} className="side bottom" />
            </div>)
          }
        </div>
      </div>

      <div className="players">
        {
          gameStore.players.map((player, p) => <div className={cn({
            'player': 1,
            active: gameStore.phase === 'normal' && gameStore.activePlayer === p,
            pullActive: gameStore.phase === 'pull' && gameStore.pullLine[gameStore.activePullPlayer] === p
          })}>
            <h2>Player { p + 1 }</h2>
            <p>Player Rainbow</p>
            <div className="game-area">
              {
                player.rainbow.map((card, c) => <div className="card">
                  <div style={{background: card[0]}} className="side top" />
                  <div style={{background: card[1]}} className="side bottom" />
                </div>)
              }
            </div>

            <p>Player Hand</p>
            <div className="player-hand">
              {
                player.hand.map((card, c) => <div className="card" onClick={() => gameStore.step(p, c)}>
                  <div style={{background: card[0]}} className="side top" />
                  <div style={{background: card[1]}} className="side bottom" />
                </div>)
              }
            </div>
            {
              gameStore.phase === 'normal' ?
                <button onClick={() => gameStore.skipStep()}>Skip step</button> :
                <button onClick={() => gameStore.skipPullStep()}>Skip pull step</button>
            }
          </div>)
        }
      </div>

      <div className="deck-wrapper">
        <h2>Deck</h2>
        <p>Num in deck - { gameStore.deck.length }</p>
        <div className="deck">
          {
            gameStore.deck.map((card) => <div className="card">
              <div style={{background: card[0]}} className="side top" />
              <div style={{background: card[1]}} className="side bottom" />
            </div>)
          }
        </div>
      </div>

      <div className="deck-wrapper">
        <h2>Discard</h2>
        <p>Num in discard - { gameStore.discard.length }</p>
        <div className="deck">
          {
            gameStore.discard.map((card) => <div className="card">
              <div style={{background: card[0]}} className="side top" />
              <div style={{background: card[1]}} className="side bottom" />
            </div>)
          }
        </div>
      </div>
    </div>
  );
};

export default observer(App);
